import React from "react";

export default class FileSlection extends React.Component {
    constructor() {
        super();
        this.state = {
            filePath: "",
        };
    }

    render() {
        const { filePath } = this.state;

        return (
            <div>
                <span>Seleziona un percorso:</span>

                <input id="FileSelection-file-path" type="file" />

                <hr />
                <span> Percorso selezionato: </span>
                {filePath}
            </div>
        );
    }
}
